izes,1);\n}\n\n$root: 14pt !default;\n$sizes:\n  1em    // 16pt\n  1.25em // 20pt\n  1.75em // 28pt\n  2.25em // 36pt\n  3em    // 48pt\n  4.5em  // 72pt\n  ;//SIZES","\n$ff-sans:  \"futura-pt\", Futura, Verdana, sans-serif;\n\n$ff-serif: \"Butler\", Georgia, serif;\n\n$ff-alt:   nth($ff-serif,2), serif;\n\n$ff-mono:  Source Code Pro, monospace;","@import \"~@/styles/base/external\";\n@import \"~@/styles/base/doc\";\n\na { color: inherit }","@import \"~@/styles/theme/colors\";\n\n$themes: cream light dark rust !default;\n\n@mixin UI_THEMES_EXTEND( $themes: $themes ){\n  @each $theme in $themes {\n    &_#{$theme} {\n      @extend %#{$theme} !optional;\n      &_translucent {\n        @extend %#{$theme}, %#{$theme}_translucent !optional;\n      }\n    }\n  }\n}\n\n%cream {\n  @extend .UiTheme;\n  color: Color(slate);\n  background: Color(cream);\n}\n%cream_translucent {\n  color: Color(slate);\n  background: rgba(Color(cream),.99);\n}\n\n%light {\n  @extend .UiTheme;\n  color: Color(slate);\n  background: Color(light);\n}\n%light_translucent {\n  color: Color(slate);\n  background: rgba(Color(light),.99);\n}\n\n%dark {\n  @extend .UiTheme;\n  color: Color(silver);\n  background: Color(dark);\n}\n%dark_translucent {\n  color: Color(silver);\n  background: rgba(Color(dark),.99);\n}\n\n%rust {\n  @extend .UiTheme;\n  color: Color(matte);\n  background: Color(alt);\n}\n%rust_translucent {\n  color: Color(matte);\n  background: rgba(Color(alt),.99);\n}\n\n.UiTheme {\n  & {\n    transition: 0s ease-out;\n  }\n  & { @include UI_THEMES_EXTEND; }\n}","$c-black: #0e0e0e;\n$c-matte: invert($c-black,99.5%);\n$c-theme: #BA9454;\n$c-alt:   #7D1214; // or: #CB3E3D\n\n$colors:(\n  dark:    $c-black, // #0E0E0E\n  slate:   lighten($c-black,15%), // #323232,\n  silver:  lighten($c-black,67%), // #B8B8B8\n  cream:   lighten(desaturate(adjust-hue($c-theme,7deg),14),42), // #F5F3ED,\n  light:   mix( white, $c-matte, 69% ), // #F6F4EE\n  matte:   $c-matte, // #F0F0F0\n  \n  theme:   $c-theme, // #BA9454\n  alt:     $c-alt,\n);\n\n@function Color($key) {\n  $color: map-get($colors,$key);\n  @return $color;\n}"],"sourceRoot":""}]);

